/* Progress Card */
.am-dashboard .progress-card {
  display: flex;
  flex-direction: column;
}

.am-dashboard .progress-chart {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 1;
}

.am-dashboard .progress-legend {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.am-dashboard .legend-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.am-dashboard .legend-color {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.am-dashboard .legend-label {
  color: #64748b;
  font-size: 0.9rem;
  flex: 1;
}

.am-dashboard .legend-value {
  color: #1e293b;
  font-weight: 600;
  font-size: 0.9rem;
}

/* Chart Card */
.am-dashboard .chart-card {
  grid-column: span 2;
}

.am-dashboard .chart-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 300px;
}

/* Loading Container */
.am-dashboard .loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
}

.am-dashboard .loading-spinner {
  width: 50px;
  height: 50px;
  border: 4px solid #e2e8f0;
  border-top: 4px solid #3b82f6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Responsive Design */
@media (max-width: 1200px) {
  .am-dashboard .dashboard-grid { grid-template-columns: repeat(auto-fit, minmax(350px, 1fr)); }
  /* Already full-width via base rules; order preserved */
}

@media (max-width: 768px) {
  .am-dashboard .sidebar-toggle-btn {
    left: 10px;
    width: 45px;
    height: 45px;
  }
  
  .am-dashboard .areas-projects-sidebar {
    width: 300px;
    left: -300px;
  }
  
  .am-dashboard .dashboard-content {
    padding: 1rem;
  }
  
  .am-dashboard .welcome-stats {
    flex-direction: column;
    gap: 1rem;
  }
  
  .am-dashboard .progress-chart {
    flex-direction: column;
    gap: 1rem;
  }
  
  .am-dashboard .chart-container {
    min-height: 250px;
  }
  
  .am-dashboard .request-meta {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
}

@media (max-width: 480px) {
  .am-dashboard .dashboard-header {
    padding: 0.5rem 1rem;
  }
  
  .am-dashboard .header-top {
    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
  }
  
  .am-dashboard .header-nav {
    flex-wrap: wrap;
    gap: 0.25rem;
  }
  
  .am-dashboard .nav-item {
    padding: 0.5rem 0.75rem;
    font-size: 0.8rem;
  }
  
  .am-dashboard .nav-item span {
    display: none;
  }
  
  .am-dashboard .dashboard-grid {
    grid-template-columns: 1fr;
  }
  
  .am-dashboard .request-item {
    flex-direction: column;
    align-items: flex-start;
  }

  /* Enhanced PM-style request item adaptation for narrow AM layout */
  .am-dashboard .request-item {
    background:linear-gradient(180deg,#f8fafc 0%,#f1f5f9 100%);
    border:1px solid #e2e8f0;
    border-radius:14px;
    padding:.85rem .95rem .95rem;
    gap:.75rem;
    position:relative;
    transition:box-shadow .25s ease, transform .25s ease, background .25s ease;
    isolation:isolate;
  }
  .am-dashboard .request-item::before {content:"";position:absolute;inset:0;border-radius:inherit;background:linear-gradient(90deg,#6366f1 0%,rgba(99,102,241,0) 35%);opacity:0;transition:opacity .3s ease;pointer-events:none;}
  .am-dashboard .request-item::after {content:"";position:absolute;top:50%;right:.8rem;width:9px;height:9px;border-right:2px solid #64748b;border-top:2px solid #64748b;transform:translateY(-50%) rotate(45deg) translateX(-2px);opacity:.25;transition:opacity .25s ease, transform .25s ease;pointer-events:none;}
  .am-dashboard .request-item:hover {background:#ffffff;border-color:#d1d9e5;box-shadow:0 8px 22px -6px rgba(15,23,42,.15);transform:translateY(-2px);} 
  .am-dashboard .request-item:hover::before {opacity:.08;} 
  .am-dashboard .request-item:hover::after {opacity:.55;transform:translateY(-50%) rotate(45deg) translateX(0);} 
  .am-dashboard .request-item:active {transform:translateY(0);box-shadow:0 4px 14px -4px rgba(15,23,42,.18);} 
  .am-dashboard .request-item:focus-visible {outline:2px solid #6366f1;outline-offset:2px;}
  
  .am-dashboard .request-status {
    align-self: flex-end;
  }
  
  .am-dashboard .sidebar-toggle-btn {
    left: 5px;
    width: 40px;
    height: 40px;
  }
  
  .am-dashboard .areas-projects-sidebar {
    width: 280px;
    left: -280px;
  }
}

/* (Removed duplicate welcome/project metrics layout block; unified earlier) */

/* Ensure metric scroller items have a consistent, slightly wider base */
.am-dashboard .project-metric-item {
  min-width: 300px;
}

/* (Removed duplicate density tweaks  consolidated earlier) */

/* Avoid nested card overlap when window height is small */
.am-dashboard .dashboard-content { padding-bottom: 0.5rem; }

/* Collapsed header should not crush nav hit areas excessively */
.am-dashboard .dashboard-header.collapsed .nav-item { padding: 0.55rem 0.9rem; }

/* Safety: prevent cards from compressing below 300px when extreme resizing occurs */
@supports (min-width: min(100%, 100%)) {
  .am-dashboard .dashboard-grid > * { min-width: 0; }
}

/* Overlap guard: if any flex children wrap strangely, allow them to expand full width */
.am-dashboard .dashboard-grid > .dashboard-card { overflow: visible; }

/* If clutter persists on very narrow but wide-height screens, stack after first two */
@media (max-width: 980px) {
  /* Redundant now but kept for safety */
  .am-dashboard .requests-card, .am-dashboard .chart-card, .am-dashboard .project-metrics-card { grid-column: 1 / -1; }
}

/* FINAL NOTE: All overrides appended at end to minimize disruption to other role dashboards */

.am-dashboard .metrics-subtitle {
  font-size: 0.8rem;
  color: #64748b;
  font-weight: 400;
}

.am-dashboard .project-metrics-container {
  min-height: 200px;
}

.am-dashboard .metrics-loading,
.am-dashboard .metrics-empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 3rem 2rem;
  text-align: center;
  color: #64748b;
}

/* Make specific cards non-transparent */
.am-dashboard .project-metrics-card,
.am-dashboard .requests-card,
.am-dashboard .chart-card {
  background: #ffffff !important;
  opacity: 1 !important;
}

.am-dashboard .metrics-loading .loading-spinner {
  width: 30px;
  height: 30px;
  border: 3px solid #e2e8f0;
  border-top: 3px solid #3b82f6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

.am-dashboard .metrics-empty svg {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #94a3b8;
}

.am-dashboard .metrics-empty span {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: #1e293b;
}

.am-dashboard .metrics-empty p {
  font-size: 0.9rem;
  color: #94a3b8;
  margin: 0;
}

.am-dashboard .project-metrics-scroll { display:flex; gap:1rem; overflow-x:auto; padding:0.75rem 0.25rem 0.75rem 0; scrollbar-width:thin; scrollbar-color:#cbd5e1 transparent; }

.am-dashboard .project-metrics-scroll::-webkit-scrollbar {
  height: 6px;
}

.am-dashboard .project-metrics-scroll::-webkit-scrollbar-track {
  background: #f1f5f9;
  border-radius: 3px;
}

.am-dashboard .project-metrics-scroll::-webkit-scrollbar-thumb {
  background: #cbd5e1;
  border-radius: 3px;
}

.am-dashboard .project-metrics-scroll::-webkit-scrollbar-thumb:hover {
  background: #94a3b8;
}

.am-dashboard .project-metric-item {
  background: white;
  border: 1px solid #e2e8f0;
  border-radius: 12px;
  padding: 1rem;
  min-width: 280px;
  flex-shrink: 0;
  transition: all 0.2s ease;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.am-dashboard .project-metric-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
}

.am-dashboard .metric-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
}

.am-dashboard .metric-project-info {
  flex: 1;
  min-width: 0;
}

.am-dashboard .metric-project-name {
  color: #1e293b;
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.25rem;
  line-height: 1.3;
}

.am-dashboard .metric-project-details {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  font-size: 0.8rem;
  color: #64748b;
}

.am-dashboard .metric-pm {
  font-weight: 500;
  color: #3b82f6;
}

.am-dashboard .metric-area {
  color: #94a3b8;
}

.am-dashboard .metric-progress-circle {
  flex-shrink: 0;
  margin-left: 1rem;
}

.am-dashboard .progress-ring {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.am-dashboard .progress-text {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.am-dashboard .progress-percentage {
  font-size: 0.9rem;
  font-weight: 700;
  color: #1e293b;
}

.am-dashboard .metric-footer {
  border-top: 1px solid #f1f5f9;
  padding-top: 0.75rem;
}

.am-dashboard .metric-stats {
  display: flex;
  gap: 1rem;
  font-size: 0.75rem;
  color: #64748b;
}

.am-dashboard .metric-stat {
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.am-dashboard .metric-stat svg {
  font-size: 0.7rem;
  color: #94a3b8;
}